:root {
  --color-bg-secondary: var(--color-bg);
}

body {
  color: var(--color-bg-contrast);
  background-color: var(--color-bg);
  font-display: optional;
  font-family: system-ui, sans-serif;
  font-size: calc( 0.9em * (var(--body-font-size) / 100) );
}
@media (min-width: 782px) {
  body {
    font-size: calc( 1em * (var(--body-font-size) / 100) );
  }
}
body.site-boxed {
  background-color: var(--color-bg-secondary);
}
body.site-boxed .site {
  background-color: var(--color-bg);
  padding: 0px;
}
body.site-boxed:not(.shadows-disabled) body.site-boxed {
  box-shadow: 0 0 calc(60px) calc(-60px + var(--shadows) * 2 ) rgba(0, 0, 0, 0.5);
}

.wrapper {
  padding-top: 3rem;
  padding-bottom: 3rem;
}
@media (min-width: 1200px) {
  .wrapper {
    padding-top: 6rem;
    padding-bottom: 6rem;
  }
}

a.skip-link {
  position: fixed;
  z-index: 1000;
  top: 0px;
  right: 0px;
}

body.singular:not(.has-sidebar) #content.container, .container-content {
  max-width: var(--content-width);
}

body:not(.has-sidebar) .main-wrapper {
  overflow: hidden;
}
body.main-wrapper-margin-top-disabled .main-wrapper {
  padding-top: 0;
}
@media (min-width: 782px) {
  body.main-wrapper-margin-top-disabled .main-wrapper {
    margin-top: calc(var(--header-main-gap-height) * -1);
  }
}
body.main-wrapper-margin-bottom-disabled .main-wrapper {
  padding-bottom: 0;
}
body.main-wrapper-pull-under .main-wrapper {
  padding-top: 0;
}
@media (min-width: 782px) {
  body.main-wrapper-pull-under .main-wrapper {
    margin-top: calc((var(--header-main-gap-height) + var(--header-main-height)) * -1);
  }
}

.site-width-max {
  max-width: var(--site-width);
}
body.site-boxed .site-width-max {
  max-width: var(--site-box-width);
}

.cps-icon.cps-dashicon, .wp-block-latest-posts .cps-dashicon.wp-block-latest-posts__post-date::before {
  font-family: dashicons !important;
}

:root {
  --header-main-gap-height: 0px;
  --header-main-font-size: var(--body-font-size);
  --header-main-logo-text-color: var(--header-main-bg-color-contrast);
  --header-main-dropbox-bg-color: var(--header-main-bg-color);
  --header-main-dropbox-logo-text-color: var(--header-main-bg-color-contrast);
}

.nav {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
}
.nav:not(.navbar-nav) .nav-item.has-styling {
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}
.nav .nav-link {
  font-size: 1.1em;
  margin-left: 0.6666666667em;
  margin-right: 0.6666666667em;
  padding-right: 0;
  padding-left: 0;
}
.nav .menu-item > a:not(.has-styling) {
  font-weight: 500;
  font-variation-settings: "wght" 560;
  text-decoration: none;
  transition: opacity 0.5s;
}
.nav .menu-item > a:not(.has-styling):hover .nav-icon-has-bg, .nav .menu-item > a:not(.has-styling):hover .nav-icon-no-label {
  opacity: 0.8;
}
.nav .current-menu-item > a:not(.has-styling),
.nav .menu-item > a:hover {
  text-decoration: underline;
}
.nav .nav-icon {
  vertical-align: middle;
  font-size: 1.4em;
  transition: opacity 0.5s;
}
.nav .nav-icon-has-bg {
  border-radius: var(--roundness);
  padding: 0.25em;
}
.nav .nav-icon-position-after {
  margin-left: 0.5em;
}
.nav .nav-icon-position-before {
  margin-right: 0.5em;
}
.nav .nav-icon-no-label {
  margin: 0;
}
