  display: table;
  clear: both;
}
.has-drop-cap:not(:focus)::after {
  padding: 0;
}

.has-text-align-left {
  text-align: left;
}

.has-text-align-center {
  text-align: center;
}

.has-text-align-right {
  text-align: right;
}

.has-small-font-size {
  font-size: 0.813em;
}

.has-medium-font-size {
  font-size: 1.25em;
}

.has-large-font-size {
  font-size: 2.25em;
}

.has-huge-font-size {
  font-size: 2.625em;
}

.form-control, body .calendar-plus #calendar-plus-search,
body .calendar-plus #calendar-plus-category, input[type=text]:not(.form-control),
input[type=tel]:not(.form-control),
input[type=email]:not(.form-control),
input[type=search]:not(.form-control),
textarea:not(.form-control),
select:not(.form-control) {
  background-clip: initial;
}

.pt-header-main-height {
  padding-top: calc(var(--header-main-gap-height) + var(--header-main-height));
}

#wrapper-footer {
  overflow: hidden;
  position: relative;
}
body:not(.shadows-disabled) #wrapper-footer {
  box-shadow: 0 0 calc(30px) calc(-30px + var(--shadows) * 1 ) rgba(0, 0, 0, 0.5);
}
@media (min-width: 1200px) {
  #wrapper-footer {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}

#footer-full-content {
  position: relative;
  padding-bottom: 1.5rem;
  margin-bottom: 1.5rem;
}

#footer-content {
  z-index: 2;
  position: relative;
}

#footer-image-holder {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
#footer-image-holder img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
#footer-image-holder::after {
  position: absolute;
  background-color: var(--footer-main-bg-color);
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.9;
}

/*
 * Adds integration with Calendar Plus plugin.
 */
body .calendar-plus .calendar-plus-filter-bar {
  overflow: initial;
}
@media (min-width: 600px) {
  body .calendar-plus .calendar-plus-filter-bar {
    display: flex;
  }
  body .calendar-plus .calendar-plus-filter-bar > p {
    flex-grow: 1;
  }
  body .calendar-plus .calendar-plus-filter-bar p:not(:last-child) {
    margin-right: 1rem;
    margin-bottom: 1rem;
  }
}
body .calendar-plus .rbc-toolbar {
  flex-wrap: initial;
  margin-bottom: 1rem;
}
body .calendar-plus .rbc-toolbar button {
  background: var(--color-boxes);
  color: var(--color-boxes-contrast);
  border: none;
  outline-color: var(--color-boxes-accent-a);
  font-size: 0.7875em;
  margin: 0;
}
body .calendar-plus .rbc-toolbar button.rbc-active, body .calendar-plus .rbc-toolbar button.rbc-active:focus {
  background: var(--color-boxes-accent-hl);
  color: var(--color-boxes-accent-hl-contrast);
  opacity: 1;
}
body .calendar-plus .rbc-toolbar button:hover {
  background: var(--color-boxes-accent);
  color: var(--color-boxes-accent-contrast);
}
@media (max-width: 781.98px) {
  body .calendar-plus .rbc-toolbar {
    justify-content: left;
  }
  body .calendar-plus .rbc-toolbar > *:not(:last-child) {
    margin-bottom: 1rem;
  }
}
body .calendar-plus .rbc-date-cell,
body .calendar-plus .rbc-header {
  background: var(--color-boxes);
  color: var(--color-boxes-contrast);
}
body .calendar-plus .rbc-header {
  padding: 0.5rem 0.25rem;
  border-bottom-color: var(--color-bg);
  font-weight: bold;
  font-size: 1em;
}
body .calendar-plus .rbc-header > a {
  color: var(--color-boxes-contrast);
}
body .calendar-plus .rbc-header + .rbc-header {
  border-left-color: var(--color-bg);
}
body .calendar-plus .rbc-month-view {
  border-color: var(--color-boxes);
}
body .calendar-plus .rbc-month-row + .rbc-month-row {
  border-top: none;
}
body .calendar-plus .rbc-off-range {
  opacity: 0.5;
}
body .calendar-plus .rbc-off-range-bg {
  background: var(--color-bg);
}
body .calendar-plus .rbc-date-cell {
  text-align: center;
  border-bottom: var(--color-bg) 1px solid;
}
body .calendar-plus .rbc-date-cell a {
  color: var(--color-boxes-contrast);
}
body .calendar-plus .rbc-day-bg + .rbc-day-bg {
  border-left-color: var(--color-boxes);
}
body .calendar-plus .rbc-today {
  background: var(--color-boxes);
}
body .calendar-plus .rbc-event, body .calendar-plus .rbc-event.rbc-selected {
  background: var(--color-bg-accent);
  color: var(--color-bg-accent-contrast);
  transition: opacity 0.2s ease-in;
}
body .calendar-plus .rbc-event:hover, body .calendar-plus .rbc-event.rbc-selected:hover {
  opacity: 0.8;
  background: var(--color-bg-accent);
}
body .calendar-plus .rbc-row-segment {
  padding: 0 4px 1px 2px;
}
body .calendar-plus .rbc-month-view .rbc-today {
  border-bottom: var(--color-bg) 1px solid;
}
body .calendar-plus .rbc-time-view {
  border-bottom: none;
}
body .calendar-plus .rbc-time-view,
body .calendar-plus .rbc-time-content > * + * > *,
body .calendar-plus .rbc-time-content,
body .calendar-plus .rbc-timeslot-group,
body .calendar-plus .rbc-time-header-content {
  border-color: var(--color-boxes);
}
body .calendar-plus .rbc-time-view .rbc-row {
  min-height: auto;
}
body .calendar-plus .rbc-day-slot .rbc-time-slot {
  border-color: var(--color-bg);
}
body .calendar-plus .rbc-current-time-indicator {
  background-color: var(--color-boxes-accent-hl);
}
body .calendar-plus .rbc-agenda-table {
  margin-bottom: 0;
}
body .calendar-plus .rbc-agenda-view table.rbc-agenda-table thead > tr > th {
  border-bottom: none;
}

.calendarp-event-shortcode-header img {
  margin-bottom: 1rem;
}
.calendarp-event-shortcode-header .dashicons {
  text-decoration: none;
}

body .calendarp:not(#nocalendarp) .agenda-minified-calendar .calendarp-date-item .calendarp-date .calendarp-date-month {
  background: var(--color-boxes);
  color: var(--color-boxes-contrast);
  border-color: var(--color-boxes);
}
body .calendarp:not(#nocalendarp) .agenda-minified-calendar .calendarp-date-item .calendarp-date .calendarp-date-day {
  border-color: var(--color-boxes);
}

/*
 * Adds basic styling to directory module included with theme.
 */
.cp-dir-item-fields:not(:last-child) {
  margin-bottom: 1rem;
}

.cp-dir-controls {
  background-color: var(--color-boxes);
}
.cp-dir-controls fieldset {
  padding: 0.6rem;
}
@media (min-width: 782px) {
  .cp-dir-controls fieldset {
    display: flex;
  }
}

.cp-dir-control {
  margin: 0.6rem;
  margin-bottom: 1.2rem;
}
@media (min-width: 782px) {
  .cp-dir-control {
    margin-bottom: 0.6rem;
  }
}

.cp-dir-control-search {
  flex-grow: 2;
}

.cp-dir-control-select {
  flex-grow: 1;
}

.cp-dir-pagination {
  padding: 0.75rem;
  border: 1px solid var(--color-boxes);
  border-top: none;
}

.cp-dir-no-results-info {
  display: none;
  padding: 0.6rem;
  font-weight: 700;
  border: var(--color-boxes) solid 1px;
}
.cp-dir-content--no-results .cp-dir-no-results-info {
  display: block;
}

.cp-dir-no-results-info-text {
  text-align: center;
}